.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;

  &__button {
    width: 100%;
    text-transform: initial;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    @media (min-width: $sm) {
      font-size: 18px;
    }
  }

  &__item {
    position: relative;
    width: 100%;
    flex-grow: 1;

    font-family: $mainFont;

    &--50 {
      @media (min-width: $xs) {
        width: calc(50% - 30px);
      }
    }

    & .button {
      @include buttonReset();
      background: transparent;
    }

    & .form-control {
      font-family: $mainFont;
      padding: 12px 16px;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      // border: 1px solid #d0d5dd;
      /* Shadow/xs */
      color: #667085;
      box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
      border-radius: 8px;
    }
  }

  &__tooltip-label {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;

    & .form__label-name {
      display: inline-flex;
      padding-right: 8px;

    }
  }

  &__label-name {
    display: block;
    margin-bottom: 10px;
    color: #344054;
    font-weight: 700;
  }

  &__text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #344054;
  }
}
