.button {
  @include buttonReset();
  padding: 2px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  border-radius: 50px;
  background: $bg-gradient;
  color: $white-color;
  transition: $transition-base;
  border-radius: $borderRadius;

  & span {
    display: flex;
    justify-content: center;
    padding: 15px 76px;
    border-radius: $borderRadius;
    background: $bg-gradient;
  }

  &:focus,
  &:active,
  &:hover {
    transition: $transition-base;

    & span {

      background: $bg-product;
    }
  }

  &--outline {
    & span {
      display: flex;
      padding: 15px 76px;
      border-radius: $borderRadius;
      background: $bg-product;
    }

    &:focus,
    &:active,
    &:hover {
      transition: $transition-base;

      & span {
        background: $bg-gradient;
      }
    }
  }
}
