.product {
  z-index: 3;
  position: relative;

  padding: 2px;
  max-width: 359px;
  height: 100%;
  background-color: $bg-product;
  border-radius: 6px;
  transition: $transition-base;
  color: $white-color;

  &:hover {
    background: linear-gradient(98.24deg, #6ddcff 0%, #7f60f9 100%),
      linear-gradient(0deg, #18181b, #18181b);
    transition: $transition-base;

    & .button {
      & span {
        background: $bg-gradient;
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 30px 35px;
    border-radius: 6px;
    background-color: $bg-product;
  }

  &__type {
    margin-bottom: 14px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 18px,
      )
    );

    @media (min-width: $sm) {
      line-height: 30px;
    }
  }

  &__price {
    margin-bottom: 14px;
    font-weight: 700;
    font-size: 38px;
    line-height: 53px;

    @include ff(
      'font-size',
      (
        320px: 38px,
        768px: 54px,
        1200px: 60px,
      )
    );

    @media (min-width: $sm) {
      line-height: 60px;
    }
    @media (min-width: $md) {
      line-height: 66px;
    }

    & span {
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: #71717a;

      @media (min-width: $sm) {
        font-size: 16px;
        line-height: 22px;
      }

      @media (min-width: $md) {
        line-height: 24px;
      }
    }
  }

  &__text {
    padding-bottom: 16px;
    margin-bottom: 30px;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: #a1a1aa;
    border-bottom: 1px solid;

    & ul {
      padding-left: 20px;
      list-style-type: disc;
    }
  }

  &__list {
    margin-top: auto;
    margin-bottom: 60px;
  }

  &__list li {
    position: relative;
    padding-left: 32px;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      background-image: url('../images/icons/li.svg');
    }
  }

  & .button {
    & span {
      background: $bg-product;
    }
  }
}
